
body{font-family:'Rubik', 'Segoe UI', 'San Francisco', 'Calibri', Arial, sans-serif;margin:0
}
.c-hero-flex{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;height:100vh;width:100vw
}
@media (max-width: 960px){
.c-hero-flex{-ms-flex-flow:column nowrap;flex-flow:column nowrap
}
.c-hero-flex__item{width:100%
}
}
.c-hero-flex__item{-ms-flex-positive:1;flex-grow:1;background:#ccc;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;background-size:cover;box-shadow:inset 0 0 0 100vmax rgba(0,0,0,0.5);transition:.2s all;cursor:pointer;text-decoration:none
}
@media (min-width: 961px){
.c-hero-flex__item{height:100%
}
}
.c-hero-flex__item:hover{-ms-flex-positive:2;flex-grow:2;box-shadow:inset 0 0 0 100vmax rgba(169,169,169,0.2)
}
.c-hero-flex__item:hover .c-hero-flex__title{transform:scale(1.2)
}
.c-hero-flex__item:nth-child(1){background:#114357;background:linear-gradient(to bottom, #114357, #F29492)
}
.c-hero-flex__item:nth-child(2){background:#114357;background:linear-gradient(to bottom, #114357, #F29492)
}
.c-hero-flex__item:nth-child(3){background:#114357;background:linear-gradient(to bottom, #114357, #F29492)
}
.c-hero-flex__title{text-transform:uppercase;letter-spacing:.075em;color:#fff;transition:.2s all;letter-spacing:0em;font-weight:100;transition:all 0.5s ease
}
.c-hero-flex__title:hover{transform:scale(1.2)
}



